.footer-nav__link:active {
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0);
}
.footer-nav__link:focus-visible {
  outline: none;
  border: 1px solid #2d383f;
}

@media (min-width: 1200px) {
  .tours {
    width: 100%;
    padding: 119px calc(50% - 600px) 102px;
  }
}
@media (min-width: 1200px) {
  .tours__cards {
    padding-top: 58px;
  }
}
.tours__show-tours {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-weight: 700;
  gap: 12px;
}
@media (min-width: 1200px) {
  .tours__show-tours {
    padding-top: 52px;
  }
}
@media (max-width: 767px) {
  .tours__show-tours {
    font-size: 14px;
    line-height: 14px;
  }
}

.tours-card {
  color: #ffffff;
  height: auto;
}
@media (min-width: 1200px) {
  .tours-card {
    min-height: 412px;
  }
}
.tours-card__photo {
  position: absolute;
  z-index: -1;
}
.tours-card__photo::after {
  content: "";
  position: absolute;
  top: 0;
  background-image: linear-gradient(143deg, rgba(15, 20, 41, 0.33) 0%, rgba(15, 20, 41, 0) 100%);
  width: 100%;
  height: 100%;
}
.tours-card__inner {
  display: flex;
  flex-direction: column;
  justify-content: end;
  width: 100%;
  height: 100%;
}
@media (min-width: 1200px) {
  .tours-card__inner {
    padding: 28px 20px;
  }
}
.tours-card__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
  line-height: 12px;
  font-weight: 500px;
}
@media (min-width: 1200px) {
  .tours-card__top {
    padding-bottom: 23px;
  }
}
.tours-card__location {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  width: 108px;
  padding: 8px 0 9px;
  background-color: #0266c1;
  gap: 6px;
}
.tours-card__time {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.tours-card h3 {
  font-size: 24px;
  line-height: 24px;
  font-weight: 700;
}
@media (min-width: 1200px) {
  .tours-card h3 {
    width: 70%;
  }
}
@media (max-width: 767px) {
  .tours-card h3 {
    font-size: 20px;
  }
}
.tours-card__bottom {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  font-weight: 300;
  line-height: 12px;
}
@media (min-width: 1200px) {
  .tours-card__bottom {
    padding-top: 38px;
  }
}
.tours-card__bottom p {
  padding-top: 6px;
  font-size: 13px;
  line-height: 13px;
  font-weight: 700;
}
@media (max-width: 767px) {
  .tours-card__bottom p {
    font-size: 12px;
  }
}

.swiper-buttons {
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  gap: 4px;
}

.swiper-button-disabled {
  opacity: 0.3;
}

